import React, { useState } from "react";

const Reciepe = props => {
  const [recipes, setRecipes] = useState([]);
  const [recipe, setRecipe] = useState({ name: "", details: "" });
  const handleSaveData = () => {
    const splittedDetails = recipe.details.split(",");
    const customizedRecipe = { name: recipe.name, details: splittedDetails };
    setRecipes([...recipes, { customizedRecipe }]);
    props.hideRecipeForm();
  };
  const handleChange = e => {
    setRecipe({ ...recipe, [e.target.name]: e.target.value });
  };
  return (
    <div className="recipe">
      <div className="form-group">
        <label htmlFor="product-name">Nazwa artykułu:</label>
        <input
          className="form-control"
          type="text"
          id="product-name"
          name="productName"
          value={recipe.name}
          onChange={handleChange}
        />
      </div>
      <div className="form-group">
        <label htmlFor="product-recipe">
          Wypisz po przcecinku listę składników
        </label>
        <textarea
          className="form-control"
          name="productRecipe"
          id="product-recipe"
          cols="30"
          rows="10"
          value={recipe.details}
          onChange={handleChange}
        ></textarea>
      </div>
      <button onClick={handleSaveData}>Zapisz</button>
    </div>
  );
};

export default Reciepe;
