import "./styles/css/style.css";
import React, { useEffect, useState } from "react";
import Form from "./components/Form";
import Contact from "./components/Contact";
import SoftwareImplementation from "./components/SoftwareImlpementations/SofwareImplementation";

function App() {
  const [form, setForm] = useState({});
  const handleShowData = e => {
    e.preventDefault();
    console.log(form);
  };
  useEffect(() => {});
  return (
    <div className="App">
      <div className="container">
        <Form>
          <Contact setForm={setForm} form={form} />
          <SoftwareImplementation setForm={setForm} form={form} />
          <button onClick={handleShowData}>Pokaż dane</button>
        </Form>
      </div>
    </div>
  );
}

export default App;
